header {
  animation: nav-show 1s ease-out both;
  animation-timeline: scroll();
  animation-range: 0 250px;

  & > div,
  & > nav > ul > li > a {
    animation: text-change 1s ease-out both;
    animation-timeline: scroll();
    animation-range: 0 15px;
  }
}
@keyframes nav-show {
  from {
    background-color: transparent;
    opacity: 1;
  }
  to {
    backdrop-filter: blur(10px);
    webkit-backdrop-filter: blur(10px);
    background-color: color-mix(in oklab, #e4f1e9 75%, transparent);
    opacity: 1;
  }
}

/* @keyframes text-change {
  from {
    color: currentColor;
  }
  to {
    color: white;
  }
} */

@media (prefers-reduced-motion) {
  header {
    transition: none;
  }
}
